.controls {
  width: 100%;
  bottom: env(safe-area-inset-bottom);
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  display: flex;
  box-sizing: border-box;
  width: 100%;
  flex-shrink: 0;
}

.button {
  position: relative;
  overflow: hidden;

  &:not(.answered)::after {
    content: attr(data-text);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: var(--background-secondary);
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &.answered {
    background-color: var(--green-40);
  }
}
@media screen and (min-width: 1200px) {
  .controls {
    width: 45rem;
  }
}
